body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  height: 100vh;
  width: 100vw;
}
.title {
  display: grid;
  grid-template-columns: repeat(3, minmax(300px, 1fr));
  grid-template-areas: "image1 title image3";
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  width: auto;
  justify-content: center;
  border: 15px solid black;
  background-color: rgb(125, 70, 152);
  color: black;
  font-size: large;
}

.title-header{
  padding-right: 20px;
}

.title h1 {
  font-size: 70px;
  font-weight: bold;
  margin: 0;
  padding: 0;
}

.title-Subtitle {
  color: rgb(20, 19, 19);
  padding-top: 0;
  font-size: 30px;
}

.title .nav-link{
  display: inline-block;
  padding: 0.5em 1em;
  color: yellow;
  font-weight: bold;
  text-decoration: none;
  font-size: 40px;
}

.title .nav-link-active {
  color: #fff;
  border-bottom: 2px solid;
  font-size: 40px;
}

img {
  object-fit:cover;
  max-height: 300px;
}

.image4 {
  padding-left: 100px;
}

.card-details {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-areas: 
    "name name name . description description description"
    "image image image image . . stats"
    "image image image image . . stats";
}

.card-details h2 {
  grid-area: name;
  font-size: 30px;
  font-weight: bold;
  margin: 0;
  padding: 0;
}

.card-details p {
  grid-area: description;
  font-size: 20px;
  color: rgb(20, 19, 19);
  padding-top: 0;
}

.card-details img {
  grid-area: image;
  max-width: 75%;
  height: 100%;
  object-fit: cover;
}

.card-details table {
  grid-area: stats;
  border: 1px solid black;
  border-radius: 5px;
  background-color: grey;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.stats h2 {
  font-size: 30px;
  font-weight: bold;
  margin: 0;
  padding: 0;
  text-align: center;
}

/* style the cards */
.card {
  display: flex;
  flex-direction: column;
  box-shadow: 5px 5px 5px rgb(125, 70, 152);
  width: 550px;
  height: 700px;
  border: 1px solid rgb(125, 70, 152);
  border-radius: 5px;
  background-color: grey;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.card:hover {
  box-shadow: -10px -5px 10px rgb(209, 85, 147), 10px 10px 10px rgb(125, 70, 152);
  border-right: 3px solid rgb(125, 70, 152);
  border-bottom: 3px solid rgb(125, 70, 152);
  border-left: 3px solid rgb(209, 85, 147);
  border-top: 3px solid rgb(209, 85, 147);
}

.card img {
  max-width: 100%;
  max-height: 50%;
  object-fit: cover;
  border-radius: 5px;
}

.card button {
  background-color: rgb(209, 85, 147);
  box-shadow: 5px 5px 5px rgb(125, 70, 152);
  border: none;
  border-radius: 5px;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

button:hover {
  -webkit-animation: card-expander 2s;
          animation: card-expander 2s;
}


.stats {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 1rem;
  grid-template-areas: "table1 table2 table3 table4";
  width: 100%;
}

.card-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  grid-gap: 30px;
  margin: 1rem;
}

.builder {
  display: flex;
  flex-direction: column;
  background-color: black;
  width: 100%;
}

.searchBar {
  display: flex;
  flex-direction: row;
  /* justify-content: space-between; */
  margin: auto;
}

select {
  background-color:rgb(209, 85, 147);
  color: black;
  align-self: center;
  font-size: 30px;
  border-radius: 2px;
  margin: 10px;
}
input {
  background-color:rgb(209, 85, 147);
  color: black;
  align-self: center;
  font-size: 30px;
  border-radius: 2px;
  margin: 10px;
}

.Home {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-image: url('https://assets.reedpopcdn.com/Elden-Ring.jpeg/BROK/resize/1200x1200%3E/format/jpg/quality/70/Elden-Ring.jpeg');
  background-size: cover;
  height: 75%;
  width: 100%;
  padding-right: -30px;
}

